import React, { Fragment, Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Form, Card, Input, Table, Modal, DatePicker, message } from 'antd';
import { BtnSearch, BtnClear, BtnExport } from 'components/common/button';
import { 
  searchStudent,
  exportReportBusPaperStudent,
  exportReportMilitaryPendingPaperStudent,
} from 'services/exportTemplatePapers';
import { requiredRule } from 'utils/validateRules';
import moment from 'moment';

const { RangePicker } = DatePicker;

@Form.create({ name: 'ReportAcademicStatisticsPage' })
class ExportTemplatePapersPage extends Component {
  state = {
    loadingStudents: false,
    dataStudent: [],
    showModal: false,
    studentId: null,
    yearNVQS: [moment(),moment().add(1,'y')],
  };

  onChangeYearNVQS = (value) => {
    console.log(value);
    this.setState({ yearNVQS: value });
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.searchStudent(values);
    });
  };
  
  showModal = () => {
    this.setState({
      showModal: true,
    });
  };

  hideModal = () => {
    this.setState({
      showModal: false,
    });
  };

  exportBus = async () => {
    const { studentId } = this.state;
    try {
      this.setState({
        loadingStudents: true,
      });
      await exportReportBusPaperStudent(studentId);
      this.setState({
        loadingStudents: false,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loadingStudents: false,
      });
    }
  };

  exportNVQS = async () => {
    const { studentId, yearNVQS } = this.state;
    const fromYear = yearNVQS[0].year();
    const toYear = yearNVQS[1].year();
    if (fromYear < toYear) {
      try {
        this.setState({
          loadingStudents: true,
        });
        await exportReportMilitaryPendingPaperStudent(Number.parseInt(studentId), yearNVQS[0].year(), yearNVQS[1].year());
        this.setState({
          loadingStudents: false,
        });
      } catch (error) {
        console.log(error);
        this.setState({
          loadingStudents: false,
        });
      }
    } else {
      message.warn('Năm bắt đầu không được nhỏ hơn Năm kết thúc!');
    }
  };

  searchStudent = async values => {
    try {
      this.setState({
        loadingStudents: true,
      });
      const studentInfo = await searchStudent(values);
      this.setState({
        loadingStudents: false,
        studentId: studentInfo.data && studentInfo.data.id,
        dataStudent: [studentInfo.data],
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loadingStudents: false,
      });
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { loadingStudents, dataStudent, showModal, yearNVQS } = this.state;
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Thao tác',
        render: () => (
          <Fragment>
            <BtnExport title='NVQS' onClick={this.showModal} />
            <BtnExport title='Bus' onClick={this.exportBus} />   
          </Fragment> 
        ),
      },
    ];
    return (
      <Card>
        <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="Mã sinh viên">
                {getFieldDecorator(
                  'studentId',
                  { rules: [requiredRule] },
                )(<Input placeholder="Mã sinh viên" />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <div className="btn-group">
                <BtnSearch htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
              </div>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingStudents}
                dataSource={dataStudent}
                columns={columns}
                rowKey="code"
              />
            </Col>
          </Row>
        </Form>
        <Modal
          title="Năm NVQS"
          visible={showModal}
          onOk={this.hideModal}
          onCancel={this.hideModal}
        >
          <Row>
            <Col span={16}>
              <RangePicker 
                placeholder={['Từ Năm', 'Đến Năm']}
                mode={['year', 'year']}
                value={yearNVQS}
                format='YYYY'
                onChange={this.onChangeYearNVQS} 
                onPanelChange={this.onChangeYearNVQS} 
              />
            </Col>
            <Col span={4}>
              <BtnExport title='Xuất giấy NVQS' onClick={this.exportNVQS} />
            </Col>
          </Row>
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExportTemplatePapersPage />
  </Page>
);



