import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const searchStudent = ({ studentId }) =>
  request({
    prefix,
    url: `/Student/GetByCode/${studentId}`,
    method: 'GET',
    params: {},
  });

export const exportReportBusPaperStudent = studentId =>
  requestDownload({
    defaultFileName: 'ReportBus.pdf',
    prefix,
    url: `/Student/${studentId}/ExportReportBusPaperStudent`,
    method: 'POST',
    params: {},
  });

export const exportBorrowingPaperStudent = studentId =>
  requestDownload({
    defaultFileName: 'Borrowing.pdf',
    prefix,
    url: `/Student/ExportReportConfirmationPaper`,
    method: 'POST',
    data: {
      studentId,
    },
  });

export const exportReportMilitaryPendingPaperStudent = (studentId, fromYear, toYear) =>
  requestDownload({
    defaultFileName: 'ReportMilitaryPending.pdf',
    prefix,
    url: `/Student/ExportReportMilitaryPendingPaperStudent`,
    method: 'POST',
    data: {},
    params: { studentId, fromYear, toYear },
  });



